* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lora', serif;
}

html, body {
  height: 100%;
}

#root {
  max-width: 90%;
  margin: 0 auto;
  background-color: #ffffff;
  border-radius: 10px;
}

.app {
  margin-top: 30px;
  border: 1px solid #ffffff;
  border-radius: 5px;
}

::placeholder {
  color: #000000;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}

input:not([type='submit']) {
  width: 100%;
  font-size: 15px;
}

input:not([type='submit']):hover {
  background-color: #f2f2f2;
}

.basic-info-container {
  max-width: 90%;
  margin: 20px auto 30px;
}

.basic-info-container * {
  border: none;
  outline: none;
  cursor: pointer;
}

.name-container {
  margin-bottom: 10px;
}

#nameInput {
  font-size: 38px;
  font-weight: 600;
  width: 100%;
}

#nameInput::placeholder {
  font-size: 38px;
}

.about-container *::placeholder {
  font-weight: 500;
  font-size: 14px;
}

.about-container > div {
  margin-bottom: 5px;
  padding-left: 5px;
}

.about-container input {
  font-size: 14px;
  height: 25px;
}

.section-header {
  max-width: 90%;
  margin: 0 auto;
  border-bottom: 1px solid #b3b3b3;
  padding-bottom: 2px;
  font-size: 12px;
  font-weight: 600;
}

.add-info-btn {
  display: inline-block;
  margin-top: 10px;
  padding: 1px;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #ffffff;
  color: #3434ff;
  font-weight: 800;
  font-size: 14px;
}

.add-info-btn:hover {
  color: #1b8dff;
}

.section-container {
  max-width: 90%;
  margin: 0 auto 50px;
}

.modal-container {
  border: 1px solid #cacaca;
  max-width: 100%;
  margin: 5px auto 10px;
  color: #303030;
  border-radius: 5px;
}

.modal-content {
  padding: 15px;
}

.modal-content label {
  font-size: 15px;
  font-weight: 700;
  display: inline-block;
  padding-bottom: 5px;
}

.form-group {
  margin-bottom: 10px;
}

.modal-content input:not([type='submit']),
#jobDesc, #degree {
  height: 35px;
  border-radius: 5px;
  border: 1px solid #808080;
  padding-left: 5px;
  outline: none;
}

#jobDesc {
  outline: none;
  resize: none;
  overflow: hidden;
  width: 100%;
  height: 140px;
  padding-top: 5px;
}

#jobDesc::placeholder {
  color: #808080;
  font-size: 15px;
}

span {
  font-size: 14px;
}

.modal-action-btns {
  display: inline-block;
  width: 100%;
}

.save-work-btn {
  color: red;
  width: 80px;
  height: 40px;
  margin: 5px 15px 5px 0px;
  border-radius: 25px;
  border: none;
  outline: none;
  color: white;
  background-color: #0070e0;
  font-weight: 800;
  font-size: 15px;
  cursor: pointer;
}

.close-modal-btn {
  width: 95px;
  height: 40px;
  border-radius: 25px;
  outline: none;
  border: 2px solid #cacaca;
  background-color: #ffffff;
  font-weight: 800;
  font-size: 15px;
  color: #0070e0;
  cursor: pointer;
}

.rendered-info {
  margin-top: 10px;
  margin-bottom: 20px;
  padding-left: 5px;
  position: relative;
}

.work-hist-display * {
  margin-bottom: 5px;
}

.main-title-dist {
  font-weight: 800;
  font-size: 18px;
}

.date-dist {
  color: #808080;
  font-size: 13px;
}

.sub-info {
  font-size: 14px;
}

.formatted-list {
  padding-left: 20px;
  font-size: 13px;
}

#degree {
  width: 100%;
  padding: 5px;
  font-style: bold;
}

.rendered-info:hover {
  background-color: #f2f2f2;
}

.delete-btn-container {
  position: absolute;
  right: 0;
  top: 0;
}

.delete-btn {
  border: none;
  outline: none;
  background-color: white;
  color: #919191;
  cursor: pointer;
  margin-right: 5px;
}

.delete-btn:hover {
  color: #eb5b5b;
}

.rendered-info:hover .delete-btn {
  background-color: #f2f2f2;
}